import React, { useContext } from "react";
import { BrandContextProvider } from "../BrandContext";
import ThemeProviderCustom from "../ThemeProviderCustom";
import { AllBrands, AllBrandsProvider } from "./BrandsUtils";

const BrandProvider = ({ children, companyName }) => {
    const brands = useContext(AllBrands);
    return (
        <BrandContextProvider brands={brands} hostname={companyName}>
            <ThemeProviderCustom>
                {children}
            </ThemeProviderCustom>
        </BrandContextProvider>
    );
};

const StaticBrandContainer = ({ children, companyName }) => {
    return (
        <AllBrandsProvider>
            <BrandProvider companyName={companyName} >
                {children}
            </BrandProvider>
        </AllBrandsProvider>
    );
};

export default StaticBrandContainer;
